.footer_container {
  width: 100vw;
  height: 15vh;
  background: black;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.footer_heading {
  color: white;
  font-family: "AccoladeTOTMed W01 Regular";
  position: relative;
  top: 35px;
  font-size: 1.2rem;
}

@media only screen and (max-width: 949px) and (min-width: 550px),
  only screen and (max-width: 549px) and (min-width: 400px),
  only screen and (max-width: 399px) and (min-width: 350px),
  only screen and (max-width: 349px) {
  .footer_heading {
    font-size: 1rem;
    top: 25px;
  }
}
