.menu_container {
  width: 100vw;
  height: auto;
  background: black;
  transition: ease-in-out 1s all;
}

.menu_intro {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding-top: 5rem;
  padding-bottom: 4.5rem;
  transition: 1s ease all;
  width: 50%;
  max-width: 900px;
  font-family: "AccoladeTOTMed W01 Regular";
  color: white;
}

.menu_title {
  position: relative;
  left: 0;
  color: rgb(233, 197, 94);
}

.menu_detail {
  position: relative;
  border-bottom: 2px solid rgb(233, 197, 94);
  left: 0rem;
  display: flex;
}

.menu_dish {
  width: 60%;
}
.menu_rate {
  width: 20%;
  margin-left: 5rem;
}
